import { instance as updateController } from './controllers/update';

export type Request = {
  method: string,
  params: object[];
};

export class Client {
  private _controllers = new Map<string, object>();

  register(instance: object) {
    this._controllers.set(instance.constructor.name, instance);
  }

  run() {
    const reconnect = () => {
      this._ws = new WebSocket("ws://127.0.0.1:53122");
      this._ws.onmessage = this.onMessage.bind(this);
      this._ws.onerror = reconnect;
      this._ws.onopen = () => {
        const requestString = JSON.stringify({ Method: 'GameController.Run', Params: [] });
        this._ws.send(requestString);
      };
    };

    reconnect();
  }

  private onMessage(ev: MessageEvent) {
    const data = JSON.parse(ev.data) as Request;
    const [className, methodName] = data.method.split('.');
    const instance = this._controllers.get(className)! as { [key: string]: Function; };

    instance[methodName](...data.params);
  }

  private _ws!: WebSocket;
}

export const client = new Client();
client.register(updateController);
client.run();

