import { inject, provide, ref } from 'vue';
import { FileStatus } from './models/file-status';
import { UpdateFiles } from './models/update-files';

export class UpdateController {
  run(count: number, files: UpdateFiles[]) {
    this.files.value = new Map<string, FileStatus>(files.map(file => [file.uuid, {
      uuid: file.uuid,
      name: file.name,
      parts: [...Array(file.parts).keys()].map(_ => ({ percent: 0 }))
    }]));

    this.count.value = count;
  }

  progress(uuid: string, part: number, percent: number) {
    const item = this.files.value.get(uuid)!.parts[part];
    item.percent = Math.max(item.percent, percent);

    if (item.percent >= 100 && percent >= 100) {
      --this.count.value;
    }
  }

  files = ref(new Map<string, FileStatus>());
  count = ref(0);
}

const filesSymbol = Symbol();
const countSymbol = Symbol();

export const instance = new UpdateController();

export const updateControllerProvide = () => {
  provide(filesSymbol, instance.files);
  provide(countSymbol, instance.count);
}

export const updateControllerInject = () => {
  const files = inject(filesSymbol);
  const count = inject(countSymbol);

  return { files, count };
}
